.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 16px;
    text-align: center; /* Center all content initially */
    background-color: #ffffff;
    cursor: pointer;
    transition: transform 0.3s ease;
    word-wrap: break-word; /* Ensure long words break and don't overflow */
}

.card h3, .card p {
    overflow: hidden; /* Prevent overflow */
    text-overflow: ellipsis; /* Add ellipsis (...) for overflowing text */
    white-space: nowrap; /* Prevent text wrapping */
}

.card:hover {
    transform: translateY(-5px);
}

.card.expanded {
    height: auto; /* Adjust height to accommodate expanded content */
}

.card.expanded .expanded-content {
    text-align: left; /* Align expanded content to the left */
}

