.expanded-content {
    margin-top: 10px;
}

.expanded-content h4 {
    margin-bottom: 8px;
}

.expanded-content ul,
.expanded-content ol {
    padding-left: 20px; /* Indent list items */
}

.deleteButton {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 10px 18px;
    margin: 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.deleteButton:hover {
    background-color: #d32f2f;
}

